/* components */
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
  width: 100%;
  background-color: #ffffff; }
  .header-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 0; }
  .header-logo {
    position: relative;
    z-index: 100;
    width: 181px; }
    @media only screen and (max-width: 1279px) {
      .header-logo {
        width: 120px; } }
  .header-menu {
    display: flex;
    align-items: center;
    transition: 0.3s linear; }
    .header-menu.active {
      opacity: 1;
      visibility: visible;
      pointer-events: auto; }
    @media only screen and (max-width: 1079px) {
      .header-menu {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        flex-direction: column;
        justify-content: center;
        width: 100%;
        height: 100vh;
        padding: 40px 20px;
        background-color: #ffffff;
        opacity: 0;
        visibility: hidden;
        pointer-events: none; } }
    @media only screen and (max-width: 560px) {
      .header-menu {
        top: 79px;
        justify-content: flex-start;
        max-width: none; } }
  .header-nav {
    margin-right: 107px; }
    @media only screen and (max-width: 1279px) {
      .header-nav {
        margin-right: 40px; } }
    @media only screen and (max-width: 1079px) {
      .header-nav {
        margin-right: 0;
        margin-bottom: 30px; } }
    .header-nav__list {
      display: flex;
      align-items: center; }
      @media only screen and (max-width: 1079px) {
        .header-nav__list {
          flex-direction: column; } }
    .header-nav__item:not(:last-child) {
      margin-right: 30px; }
      @media only screen and (max-width: 1079px) {
        .header-nav__item:not(:last-child) {
          margin-right: 0;
          margin-bottom: 10px; } }
  .header-langs {
    position: relative;
    min-width: 62px;
    height: 24px;
    padding-right: 13px;
    text-transform: uppercase;
    user-select: none;
    cursor: pointer; }
    .header-langs.opened .header-langs__arrow {
      transform: rotate(180deg); }
    .header-langs.opened .header-langs__item {
      display: block; }
    .header-langs__arrow {
      position: absolute;
      top: 0;
      right: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%; }
    .header-langs__list {
      position: absolute;
      top: 0;
      left: 0; }
    .header-langs__item {
      display: none; }
      .header-langs__item:not(:last-child) {
        margin-bottom: 3px; }
      .header-langs__item.active {
        display: block;
        pointer-events: none; }
    .header-langs__link {
      display: flex;
      align-items: center;
      color: inherit;
      text-decoration: none; }
      .header-langs__link-icon {
        margin-right: 6px; }
  .header-burger {
    position: relative;
    z-index: 100;
    display: none;
    flex-direction: column;
    width: 24px;
    height: 18px;
    cursor: pointer; }
    @media only screen and (max-width: 1079px) {
      .header-burger {
        display: flex; } }
    .header-burger::before, .header-burger::after {
      content: "";
      position: absolute;
      top: 8px;
      left: 0;
      right: 0;
      width: 100%;
      height: 2px;
      background-color: #3a3a3a;
      transition: 0.3s linear;
      pointer-events: none; }
    .header-burger__line {
      width: 100%;
      height: 2px;
      background-color: #3a3a3a;
      transition: 0.3s linear;
      pointer-events: none; }
      .header-burger__line:not(:last-child) {
        margin-bottom: 6px; }
    .header-burger.active::before {
      transform: rotate(45deg); }
    .header-burger.active::after {
      transform: rotate(-45deg); }
    .header-burger.active .header-burger__line {
      opacity: 0; }

